export default function coursesReducer(state = initialState, action) {
  switch (action.type) {
    case TAKE_COURSES:
      return {
        ...state,
      };
    case CHANGE_COURSE:
      const { chosenCourse } = action.payload;
      return {
        ...state,
        chosenCourse
      };
    default:
      return state;
  }
}